* {
  padding: 0;
  margin: 0;
}

html,
body,
#root {
  height: 100%;
}

.home {
  height: 100vh;
  overflow-y: auto;
}

.panel-list {
  position: relative;
  display: flex;
  flex: 1 1;
  flex-direction: column;
  max-height: 100vh;
  padding: 10px;
  overflow-y: auto;
}

.panel-display-group {
  position: relative;
  position: absolute;
  display: block;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  background: #fff;
  border: 0px solid transparent;
  border-radius: 10px;
  border-radius: 2px;
  box-shadow: 0px 7px 10px rgba(91, 99, 129, 0.08);
}

.panel-container {
  overflow: auto;
}

.panel-body {
  position: relative;
  height: 700px;
}

:global {
  .ant-tabs {
    height: 100%;
  }
}

@primary-color: #1890ff;